@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lato:wght@300;400;700&family=Montserrat&display=swap');

* {
    margin: 0;
    padding: 0;
    list-style: none;
    box-sizing: border-box;
    color: #2B2D42;
    font-family: 'Lato', sans-serif;
    font-size: 18px;
}

h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: 400;
    padding-bottom: 10px;
    margin: 20px 0;
    border-bottom: 1px solid #DBA81A;
    box-shadow: 0px 15px 10px -15px black 0.25;
    text-transform: uppercase;
}

.lato{
    font-family: 'Lato', sans-serif;
    font-size: 24px;
}
.regular{
    font-weight: 400;
}
.light{
    font-weight: 300;
    color: #8D99AE;
}
.bold{
    font-weight: 700;
}

.bebas{
    font-family: 'Bebas Neue', cursive;
    font-size: 32px;
}

.ocher{
    color: #DBA81A;
}

.list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 50px;
}


@media (min-width: 997px) {
    .App{
        margin: 0 100px;
    }
}

@media (min-width: 501px) and (max-width: 996px){
    .App{
        margin: 0 50px;
    }
}
@media (max-width: 500px) {
    .App{
        margin: 0 50px;
    }
}

